<template>
  <div>
    <ul class="sidenav app-sidenav" :class="{open: value}">
      <router-link
      v-for="(link, index) in links"
      :key="index"
      tag="li"
      active-class="active"
      :to="link.url"
      :exact="link.exact"
      >
        <a class="waves-effect waves-orange pointer">{{ link.title }}</a>
      </router-link>
    </ul>
  </div>
</template>

<script>
import localizeFilter from '@/filters/localize.filter' // импортируем фильтр

export default {
  props: ['value'],
  data: () => ({
    links: [
      {title: localizeFilter('Menu_Bill'), url: '/', exact: true}, //исключить "/"
      {title: localizeFilter('Menu_History'), url: '/history',},
      {title: localizeFilter('Menu_Record'), url: '/record',},
      {title: localizeFilter('Menu_Planning'), url: '/planning',},
      {title: localizeFilter('Menu_Categories'), url: '/categories',},
    ]
  })
}
</script>